<template>
    <table style="width: 800px;margin: 0 auto; rgba(255,255,255,0.3)"><tr>
        <td><a @click="$emit('prevLesson')">&lt;</a></td>
        <td class="title"><h2>{{lesson.course.title }} - {{lesson.title}}</h2></td>
        <td><a @click="$emit('nextLesson')">&gt;</a></td>
    </tr></table>
    <iframe v-show="lesson" :src="videoUrl" 
        width="800" 
        height="450" 
        allowfullscreen="allowfullscreen"
        id="vimeo-vmy"
    />
</template>

<script>
//import Player from "@vimeo/player"
import { videoBaseUrl } from "@/db.json"

export default {
    props: {
        lesson: {
            type: Object,
            required: true
        }
    },
    emits: ["prevLesson", "nextLesson"],
    mounted() {
        this.initPlayer()
    },
    updated() {
        this.initPlayer()
    },
    methods: {
        initPlayer() {
            /*const iframe = document.getElementById('vimeo-vmy')
            console.log(iframe)
            if (iframe) {
                console.log("INIT_PLAYER")
                const player = new Player(iframe)
                player.on('ended', () => {
                    console.log("->NEXT")
                    this.$emit('nextLesson')
                })
                console.log(player)
            }*/
        }
    },
    computed: {
        videoUrl() { 
            return videoBaseUrl.replace('999999999', this.lesson.videoId) 
        },
    },
}
</script>

<style scoped>
    a {
        cursor: pointer;
        padding: 4px 8px;
        border: 1px solid black;
        background-color: rgba(255,255,255,0.3)
    }
    .title {
        width: 90%;
        text-align: center;
    }
</style>