<template>
  <table style="width: 100%">
    <tr><td>
      <div style="max-height: 500px; overflow-y: auto; text-align: left">
        <div v-for="(course) in courses" :key="course.id">
          <h2>{{ course.title }}</h2>
          <div class="lesson" v-for="lesson in courseLessons(course.id)" :key="lesson.id" :class="selectedLessonClass(lesson.id)">
            <router-link :to="{ name: 'Home', params: { lessonId: lesson.id } }">
              {{ lesson.id }} - {{ lesson.title }}
            </router-link>
          </div>
        </div>
      </div>
    </td><td style="min-width: 800px">
      <VideoShow v-if="selectedLesson" :lesson="selectedLesson" 
        @prevLesson="prevLesson" @nextLesson="nextLesson"
        />
    </td></tr>
  </table>  
</template>

<script>
import useKeydown from "@/composables/use-keydown.js"
import VideoShow from "@/components/VideoShow.vue"
import { courses } from "@/db.json"

export default {
  name: "VideosList",
  components: {
    VideoShow
  },
  props: {
    lessonId: {
      default: null
    }
  },
  mounted() {
    useKeydown([
      { key: '[', fn: () => { this.prevLesson() } },
      { key: ']', fn: () => { this.nextLesson() } },
    ])

    let idLesson = 1
    this.lessons = []
    for (let i in this.courses) {
      let course = this.courses[i]
      course.lessons.forEach((l) => {
        l.id = idLesson++
        l.idCourse = course.id
        l.course = course
        l.index = this.lessons.length
        this.lessons.push(l)
      })
    }
    if (this.lessonId)
      this.openLesson(this.getLesson(this.lessonId))
  },
  updated() {
    if (this.lessonId)
      this.openLesson(this.getLesson(this.lessonId))
  },
  data() {
    return { courses, lessons: [], selectedLesson: null }
  },
  methods: {
    getLesson(lessonId) { 
      return this.lessons.find((l) => l.id == lessonId) 
    },
    openLesson(lesson) { 
      this.selectedLesson = lesson ? lesson : null // this.lessons[index] //this.lessons.find((l) => l.id == idLesson) 
    },
    prevLesson() { 
      if (this.selectedLesson && this.selectedLesson.index) {
        const lesson = this.lessons[this.selectedLesson.index-1]
        this.$router.push({ params: { lessonId: lesson.id }})
        console.log("prevLesson"+lesson.id)
      }
    },
    nextLesson() { 
      if (this.selectedLesson && this.selectedLesson.index < this.lessons.length) {
        const lesson = this.lessons[this.selectedLesson.index+1]
        this.$router.push({ params: { lessonId: lesson.id }})
        console.log("nextLesson"+lesson.id)
      }
    },
  },
  computed: {
    courseLessons() {
      let vue = this
      return (idCourse) => vue.lessons.filter(l => l.idCourse == idCourse)
    },
    selectedLessonClass() {
      let vue = this
      return (idLesson) => (vue.selectedLesson && vue.selectedLesson.id == idLesson) ? 'selected' : ''
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .lesson {
    padding: 4px;
    border: 1px solid gray;
    cursor: pointer;
    background-color: rgb(173, 216, 230, 0.2)
  }
  .lesson a {
    display: block;
  }
  .lesson.selected {
    background-color: rgb(173, 216, 230, 0.5)
  }
</style>
